import Vue from 'vue'
import App from './App.vue'
//import './registerServiceWorker'
import router from './router'
import store from './store'
import * as ulib from "@/lib/ulib.js";

Vue.config.productionTip = false

new Vue({
  router,
  created: function () {
    console.info('vue create')
    store.subscribe(mutation => {
      if (mutation.type == "a/setva") ulib.doSetva(store, mutation.payload)
      if (mutation.type == "a/seti")  ulib.doSeti(store, mutation.payload)
      if (mutation.type == "sett")  console.warn('sub mut sett')
    })
  },
  store,
  render: h => h(App)
}).$mount('#app')
