//
<template>
  <div ref="c1" style="background-color: pink">
    <input
      type="button"
      class="in1"
      v-bind:style="style1"
      v-bind:value="val1"
      v-on:keyup="ninp"
      v-on:click.prevent="click"
    />
  </div>
</template>

<script>
export default {
  name: "Cell_b1",
  data() {
    return {
      wid: 33,
      hi: 33
    };
  },
  created() {},
  props: {
    i: Number
  },
  computed: {
    // sets: width, color, background-color
    style1() {
      let x = {};
      //x.width = this.wid + 'px'
      x.height = this.hi + "px";
      let c = "blue";
      let bg = "";
      let fw = 'normal'
      let t = this.$store.state.a.valt[this.i];
      let sel = this.$store.state.a.sel[this.i];
      let err = this.$store.state.a.err[this.i];
      let vi = this.$store.state.a.v_i;
      //console.log('c-comp-s', this.i, t, sel, err)
      if (t == "f") {
        c = 'black';
        fw = 'bold'
        //bg = 'sliver';
      }
      if (sel) bg = "rgb(170, 235, 224)";
      if (vi === this.i) {
        bg = "gold";
      }
      if (err) {
        bg = "hotpink";
        if (vi === this.i) bg = "lightsalmon";
        if (t == "f") bg = 'rgb(230,150,150)';
      }
      x["background-color"] = bg; // can't use '.' because of '-'
      x.color = c;
      x['font-weight'] = fw
      //console.log('c-comp-s x', x)
      return x;
    },
    val1() {
      // a getter into a namespaced array. seems the same as direct..
      //let t = this.$store.getters['a/getValaById'](this.i)
      let t = this.$store.state.a.vala[this.i];
      //console.log('cell-comp-v', this.i, t)
      return t;
    }
  },
  watch: {},
  methods: {
    ninp: function(e) {
      console.warn("c ninp e", this.i, e.key);
      let t = this.$store.state.a.valt[this.i];
      if (t == "f") return; // ignore if fixed
      let k = e.key;
      // restrict to number here?
      if (k == "Delete") {
        k = "";
        this.$store.commit("a/setva", { i: this.i, e: "" });
      }
      this.$store.commit("a/setva", { i: this.i, v: k, r: 1 });
    },
    click: function(e) {
      console.warn("c click", this.i);
      e.target.focus()
      let t = this.$store.state.a.valt[this.i];
      let v = this.i;
      if (t == "f") v = ""; // unset if fixed
      this.$store.commit("a/seti", v);
    }
  }
};
</script>
<style>
.in1 {
  background-color: beige;
  padding: 3px 3px;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  float: left;
  width: 33.33%;
  /* height: 33.33%; ...ng */
  /*zbox-sizing: border-box;*/
  border: 1px solid blue;
  -webkit-appearance: none;
  border-radius: 0;
}
.in1:hover {
  background-color: lightblue;
}
</style>