// ulib

function get9b(x) { return 9 * Math.floor(x / 9) }
function get3b(x) { return 3 * Math.floor(x / 3) }

//--------------------------------------------
// for a 3x3.3x3 (81) grid. could pre-comute all this..
export function getRelatedIds(ind) {
  let m = []
  // sector
  let b = get9b(ind) // base of this sector
  for (let i = b; i < 9 + b; i++) { m.push(i) }
  // row
  let sec = Math.floor(ind / 9) // our sector
  let secL = get3b(sec) // leftmost sector
  let si = ind % 9 // sector index (index within sector)
  let r = Math.floor(si / 3) // row within sector
  //console.log('dcheck-r',s,s2,si,r)
  for (let i = secL; i < 3 + secL; i++) {
    if (i == sec) continue
    let t = i * 9 + r * 3
    //console.log('dcheck-r2',t)
    for (let j = t; j < 3 + t; j++) { m.push(j) }
  }
  // col
  let secC = sec % 3 // 'col' of sector
  let sc = si % 3 // col within sector
  //console.log('dcheck-c', secC, sc)
  for (let i = 0; i < 3; i++) {
    let s4 = 3 * i + secC // generates 1,4,7 etc.
    if (s4 == sec) continue
    for (let j = 0; j < 3; j++) {
      let t = s4 * 9 + 3 * j + sc
      m.push(t)
    }
  }
  return m
}
//--------------------------------------------
// updates error state of related cells
// r:1 in payload means react (else ignore)
export function doSetva(s, p) {
  if (p.r != 1) return
  console.warn('doSetva', p)
  let a = getRelatedIds(p.i)
  //console.log('su a',a)

  // for each related, recalc for it
  a.forEach((v) => {
    if (!s.state.a.vala[v]) return // ignore empty
    let x = getRelatedIds(v) // related to the related
    let hit = ''
    for (let i = 0; i < x.length; i++) {
      if (v == x[i] || !s.state.a.vala[x[i]]) continue // ignore self or empty
      //console.log('x', v, x[i])
      if (s.state.a.vala[v] == s.state.a.vala[x[i]]) {
        hit = 1
        break
      }
    }
    s.commit('a/setva', { i: v, e: hit })
  })
}
//--------------------------------------------
// updates selected state for related cells
export function doSeti(s, p) {
  console.warn('doSeti', p, s.state.a.v_ip)
  let t = s.state.a.v_ip
  if (t !== '') {
    // unset for related of previous cell
    let a = getRelatedIds(t)
    a.forEach((v) => { if (v != t) s.commit('a/setva', { i: v, s: '' }) })
  }
  if (p !== '') {
    // set for related of this cell
    let a = getRelatedIds(p)
    a.forEach((v) => { if (v != p) s.commit('a/setva', { i: v, s: 1 }) })
  }
}