<template>
  <div>
    <div class="boxg">
      <Gri_b2 v-bind:base="0" />
      <Gri_b2 v-bind:base="9" />
      <Gri_b2 v-bind:base="2*9" />
      <Gri_b2 v-bind:base="3*9" />
      <Gri_b2 v-bind:base="4*9" />
      <Gri_b2 v-bind:base="5*9" />
      <Gri_b2 v-bind:base="6*9" />
      <Gri_b2 v-bind:base="7*9" />
      <Gri_b2 v-bind:base="8*9" />
    </div>
    <Pad1 v-show="seepad" />
    <br />
    <button @click="doGet">get 1</button>
    <button @click="doPost">check</button>
    <button @click="doclear">clear</button>
    <button @click="dofix('f')">fix</button>
    <button @click="dofix('')">unfix</button>
    <br />
<p>Msg: {{msg}}</p>
    <!-- <label>ind</label>
    <input type="text" size="3" v-model="ind" />
    <label>val</label>
    <input type="text" size="3" v-model="val" />
    <label>typ</label>
    <input type="text" size="3" v-model="typ" />
    <button @click="doclick">set val</button>  -->
    <!-- <p>vg1: {{ vg1 }} vp1: {{vp1}}</p>
    <p>vt1 {{ vt1 }}</p>
    <p>sel {{ sel }}</p>
    <p>err {{ err }}</p>-->
  </div>
</template>

<script>
import Gri_b2 from "@/components/Gri_b2.vue";
import Pad1 from "@/components/Pad1.vue";
import axios from "axios";
//import * as ulib from "@/lib/ulib.js";

console.warn("n-env", process.env.NODE_ENV);
var API_URL = "/api";
if (process.env.NODE_ENV == "development") {
  API_URL = "http://stan-u:3000/api";
}
console.warn("API_URL", API_URL);
//const API_URL = 'http://stan-u:3000/api'
//axios.defaults.headers.common['Authorization'] = 'Access-Control-Allow-Origin'

export default {
  name: "Home",
  components: {
    Gri_b2,
    Pad1,
  },
  data() {
    return {
      vg1: "",
      vp1: "",
      ind: "",
      val: "",
      typ: "",
      msg: "",
    };
  },
  computed: {
    seepad() { return this.$store.state.a.v_i !== '' },
    va1() {
      //console.log('g-comp-vg1')
      return this.$store.state.a.vala;
    },
    vt1() {
      return this.$store.state.a.valt;
    },
    sel() { return this.$store.state.a.sel },
    err() { return this.$store.state.a.err },
  },
  created() {
    // this.$store.subscribe(mutation => {
    //   //subscribe can have 2nd arg: state (for what?)
    //   if (mutation.type == "a/setva") ulib.doSetva(this.$store, mutation.payload)
    //   if (mutation.type == "a/seti")  ulib.doSeti(this.$store, mutation.payload)
    //   if (mutation.type == "sett")  console.warn('sub mut sett')
    // });
  },
  methods: {
    doclick: function() {
      //console.log('dc')
      if (this.val) {
        this.$store.commit("a/setva", { i: this.ind, v: this.val })
      }
      if (this.typ) {
        this.$store.commit("a/setva", { i: this.ind, t: this.typ })
      }
    },
    doclear: function() {
      for (let i = 0; i < 81; i++)
        //if (this.$store.state.a.vala[i])
        if (this.$store.state.a.valt[i] != "f") {
          // avoid storm in setva subscriber with no r:1
          this.$store.commit("a/setva", { i: i, t: "", v: "", s: "", e: "" })
        }
    },
    dofix: function(a) {
      for (let i = 0; i < 81; i++)
        if (this.$store.state.a.vala[i]) {
          this.$store.commit("a/setva", { i: i, t: a })
        }
    },
    doGet: function() {
      axios.get(`${API_URL}/sud1`).then(
        response => {
          console.log("i r", response.data);
          this.vg1 = response.data;
          //let a = this.vg1.v
          for (let i = 0; i < 81; i++) {
            //let z = this.vg1.v;
            //console.log('rr',i, z[i])
            if (this.vg1.v[i]) {
              console.log("rrx", i);
              this.$store.commit("a/setva", { i: i, t: "f", v: this.vg1.v[i], s: "", e: "" })
            } else {
              this.$store.commit("a/setva", { i: i, t: "", v: "", s: "", e: "" })
            }
          }
        }, error => {
          console.log("in err..", JSON.stringify(error));
          this.vg1 = "get error.."
        }
      )
    },
    doPost: function() {
      //  this.$store.state.a.vala
      //var ob = {1:2,5:6}
      axios
        .post(`${API_URL}/sud1`, this.$store.state.a.vala, {
          headers: { "content-type": "application/json" }
        })
        .then(
          response => {
            console.log("p res", JSON.stringify(response));
            this.vp1 = response.data;
            let t = this.vp1.nSol
            var s=''
            if (t>1) s = 'not unique'
            if (t===0) s = 'no solution'
            if (t===1) s = 'unique solution possible'
            this.msg = `${s} (tries: ${this.vp1.nPos})`
          },
          error => {
            console.log("p err", JSON.stringify(error));
          }
        );
    }
  }
}
</script>
<style>
.boxg {
  max-width: 480px;
  border: 1px solid red;
  width: 350px;
  height: 310px;
}
</style>