//
<template>
  <div class="box3">
    <input type="button" class="pad" v-bind:style="sty(1)" v-on:click="padclick(1)" value="1" />
    <input type="button" class="pad" v-bind:style="sty(2)" v-on:click="padclick(2)" value="2" />
    <input type="button" class="pad" v-bind:style="sty(3)" v-on:click="padclick(3)" value="3" />
    <input type="button" class="pad" v-on:click="padclick('del')" value="del" />
    <input type="button" class="pad" v-bind:style="sty(4)" v-on:click="padclick(4)" value="4" />
    <input type="button" class="pad" v-bind:style="sty(5)" v-on:click="padclick(5)" value="5" />
    <input type="button" class="pad" v-bind:style="sty(6)" v-on:click="padclick(6)" value="6" />
    <input type="button" class="pad" v-on:click="padclick('esc')" value="esc" />
    <input type="button" class="pad" v-bind:style="sty(7)" v-on:click="padclick(7)" value="7" />
    <input type="button" class="pad" v-bind:style="sty(8)" v-on:click="padclick(8)" value="8" />
    <input type="button" class="pad" v-bind:style="sty(9)" v-on:click="padclick(9)" value="9" />
    <input type="button" class="pad" value=' '/> 
  </div>
</template>

<script>
import * as ulib from "@/lib/ulib.js";
export default {
  name: "Pad1",
  data() {
    return {};
  },
  created() {},
  props: {},
  computed: {
    //seepad() { return this.$store.state.a.v_i !== '' },
    ids () {
      let a = ulib.getRelatedIds(this.$store.state.a.v_i)
      var m = []
      a.forEach(e => {
        let t = this.$store.state.a.vala[e]
        if (t) m.push(t)
      })
      //console.log('pad.ids',m)
      return m
    },
  },
  methods: {
    padclick: function(v) {
      var i = this.$store.state.a.v_i;
      console.info("padclick", v, i);
      var t = this.$store.state.a.valt[i];
      if (t == "f") return; // ignore if fixed
      if (v == "del") {
        v = "";
        this.$store.commit("a/setva", { i: i, e: "" });
      }
      if (v == "esc") {
        this.$store.commit("a/seti", "");
        return;
      }
      if (i !== "") this.$store.commit("a/setva", { i: i, v: v, r: 1 });
    },
    sty: function(v) {
      //console.log("sty", v)
      var bg = ''
      //if (this.x.includes(v)) bg = 'red'  ..int string mess..
      for (var i=0; i< this.ids.length; i++) {
        if (this.ids[i] == v) {
          bg = 'silver'
          break
        }
      }
      return `background-color: ${bg};`
    }
  }
};
</script>
<style scoped>
.box3 {
  margin-left:100px; 
  padding: 3px 3px;
  border: 1px solid #73ad21;
  width: 120px;
  height: 90px;
}
.pad {
  background-color: beige;
  border: 1px solid black;
  padding: 1px 1px;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  width: 25%;
  height: 30px;
  -webkit-appearance: none;
  border-radius: 0;
}
.pad:hover {
  background-color: rgb(218, 191, 191);
}
.pad:test {
  background-color: rgb(231, 158, 153);
}
</style>