import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

var moduleA = { // const or var??
  namespaced: true,
  state: {
    vala: [],
    valt: [],
    sel: [],
    err: [],
    v_i: '',
    v_ip: '', // previous v_i
  },
  mutations: {
    setva: (state, v) => {
      //console.log('setva',v)
      if (v.t !== undefined) { Vue.set(state.valt, v.i, v.t) }
      if (v.v !== undefined) { Vue.set(state.vala, v.i, v.v) }
      if (v.s !== undefined) { Vue.set(state.sel, v.i, v.s) }
      if (v.e !== undefined) { Vue.set(state.err, v.i, v.e) }
      //state.v_i = v.i
    },
    seti: (state, v) => {
      state.v_ip = state.v_i // previous i
      state.v_i = v
    },
  },
  actions: {},
  getters: {
    // doesn't seem any different to the direct get..
    //getValaById: (state) => (i) => { return state.vala[i] }
  },
}

export default new Vuex.Store({
  plugins: [createPersistedState()],
  modules: {
    a: moduleA,
  },
  state: { 
    t1: '',
  },
  mutations: {
    sett: (state, v) => {
      state.t1 = v
    },
  },
  actions: {},
})